

.app__works {
  flex: 1;
  width: 100%;
  flex-direction: column;

}

.app__work-filter {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin: 4rem 0 2rem;

  .app__work-filter-item {
    padding: 0.5rem 1rem;
    border-radius: 0.5rem;
    background-color:var(--color-primary);
    color: #000;
    font-weight: 800;

    cursor: pointer;
    transition: all 0.3s ease;
    margin: 0.5rem;

    &:hover {
      background-color:white;
      color:black;
    }

    @media screen and (min-width: 2000px) {
      padding: 1rem 2rem;
      border-radius: 0.85rem;
    }
  }

  .item-active {
    background-color: #fff;
    color: black;
  }
}

.app__work-portfolio {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  border-color: #000;
  border-radius: 0.5rem;


  .app__work-item {
    width: 270px;
    flex-direction: column;
    margin: 2rem;
    padding: 1rem;
    border-radius: 0.5rem;
    background-color: lightskyblue;
    color:black;
    cursor: pointer;
    transition: all 0.3s ease;
    border-color: #000;

    &:hover {
      box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
    }

    @media screen and (min-width: 2000px) {
      width: 470px;
      padding: 1.25rem;
      border-radius: 0.75rem;
    }

    @media screen and (max-width: 300px) {
      width: 100%;
      margin: 1rem;
    }
  }
}

.app__work-img {
  width: 100%;
  height: 230px;
  border-radius: 0.5rem;
  border-color: #000;
  position: relative;
  background-color: #fff;

  img {
    width: 100%;
    height: 100%;
    border-radius: 0.5rem;
    object-fit: cover;
border-color: #000;

  }

  @media screen and (min-width: 2000px) {
    height: 350px;
  }
}

.app__work-hover {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);

  border-radius: 0.5rem;
  opacity: 0;
  transition: all 0.3s ease;

  div {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: rgba(0, 0, 0, 0.5);
    color: var(--color-primary);

    margin: 1rem;
    font-weight: 800;
    cursor: pointer;
    transition: all 0.3s ease;

    svg {
      width: 100%;
      height: 100%;
      color: var(--white-color);
    }
  }
  
}


.app__work-content {
  padding: 0.5rem;
  width: 100%;
  position: relative;
  flex-direction: column;
  

  h4 {
    margin-top: 1rem;
    line-height: 1.5;
background-color: var(--color-primary);
border-radius: 11px;
text-align: center;
    @media screen and (min-width: 2000px) {
      margin-top: 3rem;
    }
  }
  .p-text-desc {
    margin-top: 1rem;
    line-height: 1.5;
background-color:var(--color-primary);
border-radius: 5px;
text-align: center;
    @media screen and (min-width: 2000px) {
      margin-top: 3rem;
    }
  }

  .app__work-tag {
    position: absolute;

    padding: 0.5rem 1rem;
    border-radius: 10px;
    background-color:var(--color-primary);
    top: -25px;
    display: flex;
    flex-direction: row;
    gap: 1rem;
  }

}

.p-code{
  border-radius: 4px;
  padding:4px;
  color: black;
  background-color: #fff;
  margin-left: 0.8rem;


}
.p-code:hover{
  border-radius: 4px;
  color: black;
  background-color:lightskyblue;

}